<template>
  <div>
    <!-- 单选题 -->
    <div class="selects" v-if="questionType===1">
      <div class="content">
        <div class="prompt">请选择正确的答案</div>
        <div class="all-select">
          <div
            class="select-item"
            v-for="(item, i) in selectOptions"
            :key="i"
            :class="answerValue === item? 'singel-selected':''"
            @click="selectAnswer(item)"
          >{{item}}</div>
        </div>
      </div>
    </div>
    <!-- 多选题 -->
    <div class="selects" v-else-if="questionType===2">
      <div class="content">
        <div class="prompt">请选择正确的答案</div>
        <div class="all-select">
          <div
            class="select-item"
            v-for="(item, i) in selectOptions"
            :key="i"
            :class="answerValue.indexOf(item) > -1 ? 'singel-selected' : ''"
            @click="selectAnswer(item)"
          >{{item}}</div>
        </div>
      </div>
    </div>

    <!-- 判断题 -->
    <div class="answers" v-else-if="questionType===3">
      <div class="tt">请选择正确的答案</div>
      <div class="buttons">
        <div
          v-for="(item, i) in selectOptions"
          :key="i"
          :class="answerValue === item ? 'selected' : ''"
          @click="selectAnswer(item)"
        >{{item}}</div>
      </div>
    </div>

    <!-- 问答题 -->
    <div class="answers" v-else>
      <div class="tt">
        请将您的答案写在下方的文本框中
        <van-icon
          v-if="(contentType===2 && selectValue && ableReply)"
          name="delete"
          class="answer_delete"
          size="1.5rem"
          color="#f44"
          @click="deleteImage"
        />
      </div>
      <div class="input" v-if="contentType===1 || (contentType===2 && !selectValue )">
        <textarea
          type="text"
          @input="answerChange"
          maxlength="240"
          v-model="selectValue"
          placeholder="请输入作答内容..."
        ></textarea>

        <i class="iconfont icontupian" @click="selectImage"></i>

        <span>{{txtLength}}/240</span>
      </div>
      <div v-else class="answer_img">
        <img v-if="imgBase" :src="imgBase" alt />
        <img v-else :src="getImageFile(selectValue)" alt />
      </div>
    </div>
    <slot></slot>
  </div>
</template>
<script>
import { Icon, Uploader } from "vant";
import { ContentType } from "@/utils/enum";
import { newsetDesc } from "@/utils/index";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  components: { [Uploader.name]: Uploader, [Icon.name]: Icon },
  props: {
    questionType: {
      type: Number,
      default: 1
    },
    questionOption: {
      type: Number,
      default: 0
    },
    btnName: {
      type: String,
      default: "提交"
    },
    ableReply: {
      type: Boolean,
      default: true
    },
    answerType: {
      type: Number,
      default: 1
    },
    modulePicture: {
      type: Number,
      default: 1
    },
    answerValue: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      // singleChoice: ["A", "B", "C", "D"],
      // multiChoice: ["A", "B", "C", "D"],
      judgeChoice: ["对", "错"],
      multiValue: this.selectValue ? this.selectValue.split("") : [],
      selectValue: this.answerValue,
      contentType: this.answerType,
      imgBase: ""
    };
  },
  mounted() {
    this.$nextTick(() => {
      window.uploadImage = imgFile => {
        this.afterRead(imgFile);
      };
    });
  },
  methods: {
    selectImage() {
      sendToAPP(FuncType.albumOpen, "", 1, 1);
    },
    selectAnswer(item) {
      if (!this.ableReply) return;
      if (this.questionType === 2) {
        let index = this.multiValue.indexOf(item);
        if (index > -1) {
          this.multiValue.splice(index, 1);
        } else {
          this.multiValue.push(item);
        }
        this.selectValue = this.multiValue.sort().join("");
      } else {
        this.selectValue = item;
      }
      this.backValue();
    },
    answerChange() {
      this.backValue();
    },
    backValue() {
      this.$emit("change", {
        contentType: this.contentType,
        content: this.selectValue
      });
    },
    deleteImage() {
      this.contentType = this.answerType;
      this.selectValue = "";
      this.backValue();
    },
    afterRead(file) {
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        // file = file["content"];
        let reqData = {
          funType: this.modulePicture,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse
          .upload(reqData)
          .then(res => {
            if (res.Result) {
              this.contentType = ContentType.Picture;
              this.imgBase = file;
              this.selectValue = res.Data;

              this.backValue();
            }
          })
          .catch(err => {});
      }
    },
    getImageFile(fileId) {
      this.$gatewayHttp.resourse.picture(fileId, this.modulePicture);
    }
  },
  computed: {
    txtLength() {
      return this.selectValue.length;
    },
    selectOptions() {
      if (this.questionType === 1) {
        return newsetDesc(this.questionOption);
      } else if (this.questionType === 2) {
        return newsetDesc(this.questionOption);
      } else if (this.questionType === 3) {
        return this.judgeChoice;
      }
    }
  },
  watch: {
    answerType(newVal, oldVal) {
      if (newVal !== this.contentType) {
        this.contentType = newVal;
      }
    },
    answerValue(newVal, oldVal) {
      if (newVal !== this.selectValue) {
        this.selectValue = newVal;
      }
    }
  }
};
</script>

<style lang='less' scoped>
.selects {
  overflow: hidden;
  width: 100vw;
  padding: 2vh 0;
  background: rgba(255, 255, 255, 1);
  margin-bottom: 2vh;
  .content {
    width: 92vw;
    margin: 0 auto;
  }
  .prompt {
    text-align: left;
    font-size: 1.2rem;
    font-family: MicrosoftYaHei;
    color: rgba(51, 51, 51, 1);
  }
  .all-select {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
    .select-item {
      width: 40px;
      height: 40px;
      background: rgba(221, 228, 227, 1);
      display: inline-block;
      line-height: 40px;
      text-align: center;
    }
    .singel-selected {
      color: #fff;
      background: rgb(24, 149, 233);
    }
  }
}

.answers {
  width: 92%;
  padding: 1.5rem;
  padding: 4vw;
  background: rgba(255, 255, 255, 1);
  .tt {
    font-size: 1.2rem;
    color: #333333;
    margin-bottom: 4vw;
    .answer_delete {
      // right: 0.2rem;
      top: 0.2rem;
    }
  }
  .buttons {
    display: flex;
    justify-content: space-around;
    div {
      width: 100px;
      height: 40px;
      background: rgba(221, 228, 227, 1);
      border-radius: 3px;
      font-size: 15px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color: #333;
      text-align: center;
      line-height: 40px;
    }
    .selected {
      color: #fff;
      background: rgb(24, 149, 233);
    }
  }
  .input {
    color: #999;
    position: relative;

    textarea {
      width: 92vw;
      height: 21rem;
      font-size: 1.25rem;
      line-height: 1.5;
      background: rgba(235, 240, 239, 1);
      border-radius: 0.5rem;
      box-sizing: border-box;
      border: none;
      padding: 0.5rem;
    }
    i {
      position: absolute;
      left: 0.5rem;
      bottom: 0.1rem;
    }
    span {
      position: absolute;
      right: 0.9rem;
      bottom: 0.1rem;
    }
  }
  .answer_img {
    position: relative;
    img {
      max-width: 92vw;
      max-height: 30vh;
    }
  }
}
</style>


